@use 'base/variables' as v;
@use 'base/mixins' as m;

.menu {
    background-color: v.$grisOscuro;
    padding: 8rem 2rem;
    border-radius: 1rem;
    margin-top: -10rem;
    position: relative;
    z-index: 0;
    margin-bottom: 10rem;

    &::before {
        content: '';
        position: absolute;
        background-image: url(../img/vector.svg);
        background-repeat: no-repeat;
        width: 50%;
        height: 50%;
        z-index: -1;
        top: 0;
        right: 0rem;
    }

    @include m.tablet { 
        padding: 7rem 8rem;

        &::before {
            width: 25rem;
            height: 15rem;
            right: 15rem;
        }
    }

    @include m.desktop { 
        &::before {
            right: 35rem;
        }
    }
}