/** Media Queries **/
.text-center {
  text-align: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #111315;
}

p {
  font-size: 1.8rem;
  line-height: 1.2;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 3rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.seccion {
  margin-top: 3rem;
}

[class$=__heading] {
  color: #fef7ee;
}

[class$=__texto] {
  color: #6f757c;
}

.popular::before {
  content: "Popular";
  font-size: 1rem;
  font-weight: 700;
  color: #111315;
  text-align: center;
  align-content: center;
  position: absolute;
  width: 6rem;
  height: 2rem;
  background-color: #f6c768;
  border-radius: 2rem;
  top: 0.5rem;
  left: 1.5rem;
}

.imagen {
  width: 100%;
  height: 20rem;
}
@media (min-width: 768px) {
  .imagen {
    width: 100%;
    height: 100%;
  }
}

.menu {
  background-color: #1b1d1f;
  padding: 8rem 2rem;
  border-radius: 1rem;
  margin-top: -10rem;
  position: relative;
  z-index: 0;
  margin-bottom: 10rem;
}
.menu::before {
  content: "";
  position: absolute;
  background-image: url(../img/vector.svg);
  background-repeat: no-repeat;
  width: 50%;
  height: 50%;
  z-index: -1;
  top: 0;
  right: 0rem;
}
@media (min-width: 768px) {
  .menu {
    padding: 7rem 8rem;
  }
  .menu::before {
    width: 25rem;
    height: 15rem;
    right: 15rem;
  }
}
@media (min-width: 1024px) {
  .menu::before {
    right: 35rem;
  }
}

.enlaces {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.enlaces .enlace-gris {
  background-color: #6f757c;
  font-size: 1.2rem;
  padding: 1rem;
  font-weight: 600;
  text-align: center;
  color: #fef7ee;
  transition: background-color 0.3s ease;
  border-radius: 1rem;
}
.enlaces .enlace-gris:hover {
  background-color: #575c61;
}
.enlaces .enlace-negro {
  background-color: #1b1d1f;
  font-size: 1.2rem;
  padding: 1rem;
  font-weight: 600;
  text-align: center;
  color: #fef7ee;
  transition: background-color 0.3s ease;
  border-radius: 1rem;
}
.enlaces .enlace-negro:hover {
  background-color: #030404;
}

.productos {
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .productos {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.producto {
  margin-bottom: 7rem;
}
.producto:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .producto {
    margin-bottom: 0;
  }
}
.producto__imagen {
  position: relative;
}
.producto__imagen img {
  width: 30rem;
  height: 20rem;
  object-fit: cover;
  border-radius: 1rem;
  margin: 0 auto;
}
.producto__info {
  margin-top: 1rem;
}
.producto__nombre {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.6rem;
  color: #fef7ee;
  font-weight: 600;
}
.producto__nombre span {
  color: #111315;
  background-color: #bee3cc;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
}
.producto__calificacion {
  color: #fef7ee;
  font-size: 1.4rem;
  font-weight: 600;
  margin: 0 0 0 2rem;
  position: relative;
}
.producto__calificacion::before {
  content: "";
  position: absolute;
  background-image: url("../img/star_fill.svg");
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  left: -2rem;
  bottom: 8%;
}
.producto__calificacion--no::before {
  content: "";
  position: absolute;
  background-image: url("../img/star.svg");
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  left: -2rem;
  bottom: 8%;
}
.producto__calificacion span {
  color: #6f757c;
}
.producto__disponible {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.producto__sold-out {
  font-size: 1.4rem;
  color: #ed735d;
  margin: 0;
}