@use 'base/variables' as v;
@use 'base/mixins' as m;

.productos {
    padding: 0 2rem;

    @include m.tablet { 
        @include m.grid(2, 4rem);
    }

    @include m.desktop { 
        @include m.grid(3, 4rem);
    }
}

.producto {
    margin-bottom: 7rem;
    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet { 
        margin-bottom: 0;
    }

    &__imagen {
        position: relative;

        img {
            width: 30rem;
            height: 20rem;
            object-fit: cover;
            border-radius: 1rem;
            margin: 0 auto;
        }
    }

    &__info {
        margin-top: 1rem;
    }

    &__nombre {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 1.6rem;
        color: v.$blanco;
        font-weight: 600;

        span {
            color: v.$negro;
            background-color: v.$verde;
            padding: .5rem 1rem;
            border-radius: .3rem;
        }
    }

    &__calificacion {
        color: v.$blanco;
        font-size: 1.4rem;
        font-weight: 600;
        margin: 0 0 0 2rem;
        position: relative;

        &::before {
            @include m.rating('../img/star_fill.svg');
        }

        &--no {
            &::before {
                @include m.rating('../img/star.svg');
            }
        }

        span {
            color: v.$gris;
        }
    }

    &__disponible {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__sold-out {
        font-size: 1.4rem;
        color: v.$naranja;
        margin: 0;
    }
}