@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    background-color: v.$negro;
}
p {
    font-size: 1.8rem;
    line-height: 1.2;
}
.contenedor {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: 0 0 2rem  0 ;
}
h1 {
    font-size: 3rem;
}
h3 {
    font-size: 2.8rem;
    text-align: center;
    text-transform: uppercase;
}
ul {
    padding: 0;
    list-style: none;
}
.seccion {
    margin-top: 3rem;
}
[class$="__heading"] {
    color: v.$blanco;
}
[class$="__texto"] {
    color: v.$gris;
}
.popular {
    &::before {
        content: 'Popular';
        font-size: 1rem;
        font-weight: 700;
        color: v.$negro;
        text-align: center;
        align-content: center;
        position: absolute;
        width: 6rem;
        height: 2rem;
        background-color: v.$amarillo;
        border-radius: 2rem;
        top: .5rem;
        left: 1.5rem;
    }
}
.imagen {
    width: 100%;
    height: 20rem;
    
    @include m.tablet { 
        width: 100%;
        height: 100%;
    }
}